import React from "react";
import Card from "./partials/Card";

function Features(){
  return (
    <div className="row" id="features" >
    <div className="col-lg-12">
    <h2 className="featureTitle">Features made for all players...</h2>
    </div>
    <Card src="./images/cyberStyle1.png" alt="feature 1" cardTitle="Casuals" text="For those who want the feel of community more than playing." />
    <Card src="./images/cyberStyle2.png" alt="feature 1" cardTitle="Softcores" text="Perfect for those who just want to easily jump in and out of games." />
    <Card src="./images/cyberStyle3.png" alt="feature 1" cardTitle="Casual-Hardcores" text="Find like minded individuals that are hardcore but not full crazy." />
    <Card src="./images/cyberStyle4.png" alt="feature 1" cardTitle="Hardcore-Hardcores" text="Find and contest with others to prove you are the hardcorest of hardcores in your fav games." />
    </div>
  )
}

export default Features;
